.search-results-container {
  background-color: #FCF9F3;
  height: 100vh;
  overflow: scroll;
}

.search-results-view .search-field {
  padding-top: 100px;
}

.recipe {
  margin: 30px;
  width: 15%;
}

.recipe-results-container {
  -ms-flex-align: end;
      align-items: flex-end;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 30px;
}

.recipe-search-results {
  background-repeat: no-repeat;
  background-size: cover;
  border: 0.5px solid slategrey;
  border-radius: 6px;
  height: 120px;
  margin: 15px;
}

.recipe:hover {
  color: #F93F23;
}

.recipe-search-results:hover {
  cursor: pointer;
}

.time-rating {
  color: #666666;
  display: none
}

/*.recipe-search-results:hover .time-rating {
  display: block;
  z-index: 3;
  animation-name: fadeIn;
  animation-duration: 3s;
}*/

/*.recipe-background {
  display: none;
}*/

.recipe-search-results:hover .time-rating {
  background: #f2f2f2;
  border-radius: 6px;
  display: block;
  height: 100%;
  opacity: 0.8;
  position: relative;
  top: -16px;
  width: 100%;
  z-index: 2;
  -webkit-animation-name: info-container-slide;
          animation-name: info-container-slide;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}

@-webkit-keyframes info-container-slide {
  0% {
    margin-right: -100px;
    height: 0%;
  }
  100% {
    margin-right: 0px;
    height: 100%;
  }
}

@keyframes info-container-slide {
  0% {
    margin-right: -100px;
    height: 0%;
  }
  100% {
    margin-right: 0px;
    height: 100%;
  }
}



